@import url("https://fonts.googleapis.com/css?family=Muli:400,400i,600,700,700i,800");

@media (prefers-color-scheme: dark) {
  :root {
    /* Colours */
    --color-primary: #3eb0ef;
    --color-base: #fff;
    --color-border: #ddd;
    --color-bg: #000;
    --color-second: rgba(255, 255, 255, 0.3);

    /* Fonts */
    --font-sans-serif: "Muli", sans-serif;
    --font-serif: Georgia, Times, serif;
    --font-mono: Menlo, Courier, monospace;
    --font-light: 100;
    --font-normal: 400;
    --font-bold: 700;
    --font-heavy: 800;

    /* Breakpoints */
    --xlarge: 1680px;
    --large: 1280px;
    --medium: 980px;
    --small: 740px;
    --xsmall: 480px;

    /* Sizes */
    --height: 4rem;
    --margin: 2rem;
    --radius: 0.5rem;
  }
}
@media (prefers-color-scheme: light) {
  :root {
    /* Colours */
    --color-primary: #3eb0ef;
    --color-base: #131313;
    --color-border: #ddd;
    --color-bg: #f8f8f8;
    --color-second: rgba(255, 255, 255, 0.3);

    /* Fonts */
    --font-sans-serif: "Muli", sans-serif;
    --font-serif: Georgia, Times, serif;
    --font-mono: Menlo, Courier, monospace;
    --font-light: 100;
    --font-normal: 400;
    --font-bold: 700;
    --font-heavy: 800;

    /* Breakpoints */
    --xlarge: 1680px;
    --large: 1280px;
    --medium: 980px;
    --small: 740px;
    --xsmall: 480px;

    /* Sizes */
    --height: 4rem;
    --margin: 2rem;
    --radius: 0.5rem;
  }
}
